/* --------------顏色設定開始--------------- */
$primary-color:#374A87;
$primary-light-color:#7183AC;
$secondary-color:#6FABE6;
$secondary-light-color:#8CACD6;
$special-color:#B39DD9;
$bright-color:#7DBF71;
$assist-color:#51BDC5;
$assist-dark-color:#61A7BE;
$danger-color:#E57676;
$danger-light-color:#EA9B5E;
$danger-dark-color:#EE404C;
$stable-color:#95634A;
$base-color:#8d8d8d;
$stable-light-color:#BC8E79;
$stable-xlight-color:#d6ad98;
$tertiary-color:#3E659F;
$white:#fff;
$pale-color:#F6F6F6;
$gray-xxxlight:#F1F1F1;
$gray-xxlight:#EAEAEA;
$gray-xlight:#DBDBDB;
$gray-light:#BDBDBD;
$gray:#666;
$gray-dark:#333;
$black:#000;

$primary-weak-color:#72B7F3;
$primary-xlight-color:#D9F0FF;
$primary-xxlight-color:#F2FBFB;
$secondary-dark-color:#6DA95C;
$secondary-xdark-color:#42AB26;
$secondary-pale-color:#F5FBF4;

$base-light-color:#9FB1C2;
$pale-light-color:#f8f8f7;


$color-themes: (
  //屬性: key, value
  'primary-color': $primary-color,
  'primary-light-color': $primary-light-color,
  'primary-xlight-color': $primary-xlight-color,
  'primary-xxlight-color': $primary-xxlight-color,
  'primary-weak-color': $primary-weak-color,
  'base-color': $base-color,
  'base-light-color': $base-light-color,
  'secondary-color': $secondary-color,
  'secondary-dark-color': $secondary-dark-color,
  'secondary-xdark-color': $secondary-xdark-color,
  'secondary-light-color': $secondary-light-color,
  'tertiary-color': $tertiary-color,
  'assist-color': $assist-color,
  'assist-dark-color': $assist-dark-color,
  'danger-color': $danger-color,
  'danger-dark-color': $danger-dark-color,
  'danger-light-color': $danger-light-color,
  'bright-color': $bright-color,
  'pale-color': $pale-color,
  'pale-light-color': $pale-light-color,
  'special-color': $special-color,
  'stable-color': $stable-color,
  'stable-light-color': $stable-light-color,
  'white': $white,
  'gray-xxxlight': $gray-xxxlight,
  'gray-xxlight': $gray-xxlight,
  'gray-xlight': $gray-xlight,
  'gray-light': $gray-light,
  'gray': $gray,
  'gray-dark': $gray-dark,
  'black': $black,
);
/* --------------字型設定開始--------------- */
$font-xxxs:.7rem;
$font-xxs:.8rem;
$font-xs:.9rem;
$font-xsm:.95rem;
$font-sm:1rem;
$font-smd:1.125rem;
$font-md:1.2rem;
$font-lg:1.4rem;
$font-xl:1.8rem;
$font-xxl:2.2rem;
$font-xxxl:3rem;

$font-size-themes: (
  //屬性: key, value
  'font-xxxs': $font-xxxs,
  'font-xxs': $font-xxs,
  'font-xs': $font-xs,
  'font-xsm': $font-xsm,
  'font-sm': $font-sm,
  'font-smd': $font-smd,
  'font-md': $font-md,
  'font-lg': $font-lg,
  'font-xl': $font-xl,
  'font-xxl': $font-xxl,
  'font-xxxl': $font-xxxl,
);


$font-xlight:100;
$font-light:400;
$font-bold:500;
$font-heavy:600;
$font-xheavy:800;

$font-weight-themes: (
  //屬性: key, value
  'font-xlight': $font-xlight,
  'font-light': $font-light,
  'font-bold': $font-bold,
  'font-heavy': $font-heavy,
  'font-xheavy': $font-xheavy,
);


@mixin gradient-horizon($primary-color,$gray) {
background: $gray; /* Old browsers */
background: -moz-linear-gradient(left,  $primary-color 0%, $gray 100%); /* FF3.6-15 */
background: -webkit-linear-gradient(left,  $primary-color 0%,$gray 100%); /* Chrome10-25,Safari5.1-6 */
background: linear-gradient(to right,  $primary-color 0%,$gray 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */

}

/* 字體設定 */
$font1: 微軟正黑體, Noto Sans TC, sans-serif;
$font2: Noto Sans TC, 微軟正黑體, sans-serif;
$font-en: arial, sans-serif;

/* --------------按鈕設定開始--------------- */

@mixin btn-dark-mix($bgcolor, $txt) {
  color: $txt;
  background: $bgcolor;
  &:hover {
      color: $txt;
      background: darken($color: $bgcolor, $amount: 5%);
  }
}
@mixin btn-light-mix($bgcolor, $txt) {
  color: $txt;
  background: $bgcolor;
  &:hover {
      color: $txt;
      background: lighten($color: $bgcolor, $amount: 5%);
  }
}
@mixin btn-border($color){
  color: $color;
  border: 1px solid $color;
  padding: 5px 15px;
  &:hover,&.active{
    color: $white;
    background: $color;
    border: 1px solid $color;
  }
}
@mixin btn-border-mix($color,$bdcolor){
  color: $color;
  border: 1px solid $bdcolor;
  padding: 5px 15px;
  background: $white;
  &:hover,&.active{
    color: $white;
    background: $bdcolor;
    border: 1px solid $bdcolor;
  }
}
@mixin btn-blank($color){
  color: $color;
  background: $white;
  padding: 5px 15px;
  text-align: center;
  &:hover{
    color: $white;
    background: $color;
  }
}
/*連結設定開始*/
@mixin txt-link-dark($color){
  color: $color;
  &:hover{
    color: darken($color: $color, $amount: 10%);
  }
}
@mixin txt-link-light($color){
  color: $color;
  &:hover{
    color: lighten($color: $color, $amount: 15%);
  }
}
/*標題設定開始*/
@mixin top-title($color) {
  font-size: $font-xl;
  letter-spacing: 8px;
  font-weight: 600;
  color:$color;
  position: relative;
  text-align: center;
  padding-bottom: 12px;
  margin-bottom: 30px;
  @include Pad{
    font-size:$font-lg;
    margin-bottom: 22px;
  }
  &:after{
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    background: $color;
    left: 0;
    bottom: 0;
  }
}
/*表格設定開始*/
@mixin table-simple($bgcolor,$bdcolor){
  font-size: $font-sm;
  border: 2px solid $bdcolor;
  th{
      background: $bgcolor;
      border-bottom: 2px solid $bdcolor!important;
      color: $white;
      font-weight: $font-light;
      @include Pad{
        padding: .5rem;
      }
  }
  td{
    border-top: 1px solid $bdcolor;
    vertical-align: middle;
    @include Pad{
      padding: .5rem;
    }
  }
  td,th{
    border-color: $bdcolor;
  }
}