.account-form,
.password-form,
.support-form {
    min-height: calc(100% - 64px);
    @include PC {
        font-size: $font-xs;
        min-height: calc(100% - 52px);
        label {
            margin-bottom: 5px;
        }
    }
    .form-control,
    .form-select {
        border: 1px solid $gray-xlight;
    }
    .form-control[readonly] {
        background: rgba($color: $white, $alpha: 0.2);
        border: 1px solid $gray-xlight;
    }
    .btn {
        padding-top: 6px;
        padding-bottom: 6px;
        border-radius: 6px;
        height: 38px;
        @include PC {
            font-size: $font-xs;
        }
        img {
            @include PC {
                width: 16px;
            }
        }
    }
    .input-group-append {
        .btn {
            border-radius: 0 4px 4px 0;
        }
    }
}
.support-form-wrapper {
    @include mobile-lg {
        width: 100% !important;
    }
}
.password-form {
    .toggle-menu {
        border-color: $assist-dark-color;
    }
    .toggle-menu-item {
        &.active {
            background: $assist-dark-color !important;
        }
    }
}
.view-form {
    .toggle-menu {
        border-color: $assist-color;
    }
    .toggle-menu-item {
        &.active {
            background: $assist-color !important;
        }
    }
}
.case-search {
    &-item {
        display: block;
        box-shadow: 0 0 8px $gray-light;
        &:hover {
            box-shadow: none;
            .case-search-action {
                opacity: 1;
            }
        }
    }
    &-header {
        background: $primary-light-color;
        color: $white;
        display: flex;
        border-radius: 5px 5px 0 0;
        img {
            width: 15px;
            @include Pad {
                width: 12px;
            }
        }
    }
    &-body {
        border: 1px solid $gray-light;
        border-radius: 0 0 5px 5px;
        border-top: none;
        display: flex;
        align-items: flex-end;
    }
    &-info {
        display: flex;
        flex-wrap: wrap;
        color: $gray-dark;
        flex: 1 1;
        font-weight: $font-bold;
        span {
            color: $primary-light-color;
            margin-right: 8px;
        }
        &-item {
            width: 33.33%;
            margin-bottom: 8px;
            @include PC-sm {
                width: 50%;
            }
            @include Pad {
                width: 100%;
            }
        }
    }
    &-action {
        display: flex;
        flex-shrink: 0;
        border-radius: 50px;
        transition: all 0.2s ease;
        opacity: 0.8;
        width: 95px;
        height: 34px;
        color: $gray-dark !important;
        font-weight: $font-bold;
        @include Pad {
            width: 85px;
        }
        &:hover {
            color: $gray-dark !important;
        }
    }
    &-status {
        border-radius: 5px 0 0 0;
        padding: 8px 0;
        width: 123px;
        display: flex;
        justify-content: center;
        align-items: center;
        & ~ div {
            display: flex;
            justify-content: space-between;
            align-items: center;
            @include Pad {
                flex-direction: column;
                align-items: flex-start;
                justify-content: flex-start;
                padding-top: 8px;
                padding-bottom: 8px;
            }
        }
    }
}
.case-search-form {
    display: flex;
    align-items: end;
    @include PC-sm {
        flex-direction: column;
    }
    &-date {
        flex: 1 1;
        align-items: end;
        @include mobile-lg {
            width: 100%;
            flex-direction: column;
            align-items: center;
            .form-group {
                width: 100%;
            }
        }
    }
    .tab-gruop {
        flex: 0 0;
    }
    .form-box-btn {
        padding: 8px 24px;
        flex: 0 0;
        white-space: nowrap;
    }
}
.date-to {
    height: 38px;
    line-height: 38px;
    @include mobile-lg {
        height: 20px;
        line-height: 20px;
    }
}
.case-detail {
    &-header {
        color: $white;
        display: flex;
        border-radius: 5px 5px 0 0;
        background: $base-color;
        img {
            width: 15px;
            filter: brightness(200%);
            @include Pad {
                width: 12px;
            }
        }
        a{
            color: $white;
        }
    }
    &-body {
        border-radius: 0 0 5px 5px;
        border-top: none;
        background: $gray-xxxlight;
    }
    &-list {
        display: flex;
        &:last-child {
            .case-detail-info {
                border-bottom: 0;
            }
        }
    }
    &-icon {
        margin-right: 24px;
        margin-bottom: auto;
        margin-top: 16px;
    }
    &-info {
        display: flex;
        flex-wrap: wrap;
        color: $gray-dark;
        flex: 1 1;
        font-weight: $font-bold;
        border-bottom: 1px solid $gray-light;
        padding: 12px 0;
        &[data-con="column"] {
            flex-direction: column;
            padding: 19px 0;
            .case-detail-info-item {
                display: flex;
                width: 100% !important;
                &:last-child {
                    margin-bottom: 0;
                }
                span {
                    flex-shrink: 0;
                }
            }
        }
        span {
            color: $primary-light-color;
            margin-right: 8px;
        }
        &-item {
            width: 33.33%;
            margin-bottom: 8px;
            @include PC-sm {
                width: 50%;
            }
            @include Pad {
                width: 100%;
            }
        }
    }
    &-link {
        display: flex;
        flex-wrap: wrap;
        &-item {
            background: $secondary-light-color;
            border-radius: 5px;
            color: $white;
            font-weight: $font-light;
            font-size: $font-xs;
            padding: 2px 12px 4px 12px;
            margin-right: 12px;
            margin-bottom: 12px;
            &:hover {
                color: $white;
                background: $secondary-color;
            }
            &:last-child {
                margin-right: 0;
            }
        }
    }
    &-title {
        color: $primary-light-color;
        font-size: $font-sm;
        padding: 22px 0;
    }
    &-status {
        border-radius: 5px 0 0 0;
        padding: 8px 0;
        width: 123px;
        display: flex;
        justify-content: center;
        align-items: center;
        & ~ div {
            display: flex;
            justify-content: space-between;
            align-items: center;
            @include Pad {
                flex-direction: column;
                align-items: flex-start;
                justify-content: flex-start;
                padding-top: 8px;
                padding-bottom: 8px;
            }
        }
    }
}
.sign-add {
    border-left: 3px solid $primary-color;
    padding-left: 16px;
    font-weight: $font-bold;
    display: flex;
    align-items: flex-start;
    @include mobile-lg{
        flex-direction: column;
    }
    &-title {
        flex-shrink: 0;
        font-size: $font-xsm;
        margin-bottom: 0;
        line-height: 30px;
    }
    &-btn {
        display: flex;
        color: $primary-color;
        align-items: center;
        font-weight: $font-bold;
        padding: 0 8px !important;
        height: 30px !important;
        margin-left: 12px;
        &:hover {
            opacity: 0.8;
        }
    }
    &-list {
        &-item {
            display: flex;
            align-items: center;
        }
    }
}
.history {
    &-item {
        display: flex;
        align-items: flex-start;
        margin-bottom: 56px;
        position: relative;
        @include PC-sm {
            flex-direction: column;
        }
        &:last-child {
            margin-bottom: 0;
            &:after{
                display: none;
            }
        }
        &:after {
            content: "";
            display: block;
            position: absolute;
            width: 2px;
            height: calc(100% + 56px);
            left: 88px;
            top: 0;
            z-index: 0;
            background: url("../images/dots.svg") repeat-y;
            @include PC-sm {
                left: 16px;
            }
        }
        &-title {
            width: 178px;
            min-height: 85px;
            flex-shrink: 0;
            border-radius: 5px;
            text-align: center;
            color: $white;
            background: lighten($color: $base-color, $amount: 10%);
            margin-right: 24px;
            position: relative;
            padding-bottom: 16px;
            margin-bottom: 12px;
            z-index: 10;
            @include PC-sm {
                min-height: auto;
                width: 100%;
            }
            @include Pad{
                padding-bottom: 12px;
            }
            h3 {
                background: $gray-light;
                font-size: $font-sm;
                padding: 12px 8px;
                border-radius: 5px 5px 0 0;
                margin-bottom: 12px;
                @include Pad{
                    padding: 8px;
                    margin-bottom: 8px;
                }
            }
        }
        &-message {
            background: $gray-xxxlight;
            border-radius: 16px;
            display: flex;
            padding: 12px 32px;
            align-items: flex-start;
            @include PC-sm {
                padding: 8px 24px;
                margin-left: 32px;
            }
            img {
                flex-shrink: 0;
                margin-right: 16px;
                margin-top: 2px;
                @include PC-sm{
                    margin-top: 0;
                }
            }
        }
        &-content {
            width: 100%;
        }
        &[data-step="now"]{
            .history-item-title{
                background: lighten($color: $danger-light-color, $amount: 5%);
                h3{
                    background: $danger-color;
                }
            }
        }
    }
    &-info {
        display: flex;
        flex-wrap: wrap;
        font-weight: $font-bold;
        @include PC-sm {
            padding-left: 32px;
        }
        &-item {
            width: 50%;
            display: flex;
            align-items: center;
            margin-bottom: 8px;
            @include PC-sm {
                width: 100%;
                margin-bottom: 8px;
            }
            span {
                color: $primary-light-color;
                margin-right: 12px;
                font-weight: $font-bold;
            }
        }
        &-btn {
            display: flex;
            align-items: center;
            padding: 0 !important;
            height: auto !important;
            color: $primary-color;
            font-weight: $font-bold;
            img {
                transition: all 0.2s ease;
            }
            &:hover {
                color: $primary-color;
                img {
                    filter: brightness(70%) contrast(110%);
                }
            }
        }
    }
}
