@charset "utf-8";
@import url("https://fonts.googleapis.com/earlyaccess/notosanstc.css");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap");

/* --------------基本設定開始--------------- */
*,
*::before,
*::after {
	box-sizing: border-box;
}

html,
body {
	width: 100%;
	-webkit-text-size-adjust: none;
}
body {
	-webkit-font-smoothing: antialiased;
	-webkit-overflow-scrolling: touch;
	font-family: "Noto Sans TC", "Microsoft JhengHei", "sans-serif";
	line-height: 1.5;
	letter-spacing: 0.5px;
	color: $gray-dark;
}
.clear {
	clear: both;
	height: 0;
	font-size: 1px;
	line-height: 0px;
}
img {
	display: block;
	max-width: 100%;
	height: auto;
}
label{
	font-weight: $font-bold;
}
a,
a:hover,
button,
button:hover {
	transition: all 0.2s ease;
	text-decoration: none;
}
a:focus{
	outline: none;
}
p {
	margin-bottom: 0;
}
ul {
	padding-left: 0;
	list-style: none;
	margin-bottom: 0;
}
ol {
	margin-bottom: 0;
}
input,select{
	border: 1px solid $primary-light-color;
	@include PC{
		font-size: $font-xs;
	}
}
input[type=file] {
  @include PC{
		font-size: $font-xs;
	}
	&::file-selector-button{
		height: 36px;
	}
}

input::placeholder,textarea::placeholder {
	color: $gray-light!important;
	font-size: $font-xsm;
	@include PC{
		font-size: $font-xs;
	}
}
input:value{
	@include PC{
		font-size: $font-xs;
	}
}
.form-check-input{
	border: 1px solid $primary-light-color;
}
.form-check-input:checked{
	background-color: $primary-color!important;
	border: 1px solid $primary-color!important;
}
.form-select{
	background: url("../images/ic-down-arrow.svg") no-repeat right center $white;
	color: $gray-dark;
	font-size: $font-sm;
	@include PC{
		font-size: $font-xs;
	}
}
.form-control{
	font-size: $font-sm;
	color: $gray-dark;
	border: 1px solid $primary-light-color;
	&[readonly] {
	background: lighten($color: $primary-xxlight-color, $amount: 3%);
	border: 1px solid $gray-xlight;
	box-shadow: none;
}
}
.input-group-text{
	background: $white;
	border-radius: 0 3px 3px 0;
	height: 100%;
	padding: 0 8px;
	&:hover{
		opacity: 0.8;
		border-left: none;
	}
}
.datetimepicker-input{
	padding-right: 0;
}
.container{
	@include PC-sm{
		max-width: 800px;
	}
}
.modal-header{
	padding: 10px 24px;
	justify-content: space-between;
	@include mobile-lg{
		padding: 10px 12px;
		flex-direction:column-reverse;
		&[data-txt="less"]{
			flex-direction: row;
			.btn{
				flex: 0 0 81px;
			}
			.modal-title{
				width: auto;
				a{
					color: $white;
				}
			}
		}
	}
	.btn-close{
		background: url("../images/ic-cross.svg") center;
		background-size: 24px;
		opacity: 1;
		&:hover{
			opacity: 0.7;
		}
	}
}

