/*--------------------首頁開始------------------*/
.wrapper[data-con="index"]{
    background: url("../images/bg.svg") no-repeat center;
    background-size: 2050px;
    min-height: calc(100vh - 51px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @include PC {
        background: url("../images/bg.svg") repeat-y 30%;
        background-size: 1500px;
    }
    @include Pad{
        background: url("../images/bg.svg") repeat-y 40%;
        background-size: 1200px;
        min-height: calc(100vh - 51px);
    }
    @include mobile-lg{
        min-height: calc(100vh - 49px);
    }
}
.main {
    width: 90%;
    max-width: 1300px;
    padding: 50px 0 0 0;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    @include PC-lg {
        max-width: 1180px;
        padding: 0;
    }
    @include PC {
        max-width: 700px;
        flex-wrap: wrap;
    }
    @include Pad {
        max-width: 550px;
    }
    @include mobile-lg {
        padding: 10px 0 10px 0;
    }
    &-top{
        position: absolute;
        display: flex;
        right: 0;
        top: -38px;
        font-size: $font-md;
        @include PC-lg{
            right: 10px;
        }
        @include PC{
            font-size: $font-sm;
            right: 45px;
            img{
                width: 18px;
            }
        }
        @include Pad{
            right: 20px;
            font-size: $font-xs;
        }
        @include mobile-xmd{
            right: 0;
        }
    }
    &-vision {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 470px;
        @include PC {
            height: 240px;
            width: 100%;
            flex-direction: row;
            align-items: flex-start;
            position: relative;
            margin-bottom: 20px;
        }
        @include Pad {
            height: 220px;
            margin-bottom: 15px;
        }
        @include mobile-lg {
            height: 320px;
            flex-direction: column;
            align-items: center;
            margin-bottom: 25px;
        }
        @include mobile-sm {
            height: 260px;
            flex-direction: column;
            align-items: center;
            margin-bottom: 15px;
        }
        &-img {
            width: 100%;
            height: 460px;
            background: url("../images/main-vision.svg") no-repeat;
            background-size: contain;
            @include PC-lg {
                width: 90%;
                height: 410px;
            }
            @include PC-smd {
                height: 380px;
                margin-top: 10px;
            }
            @include PC {
                background: url("../images/main-vision-mobile.svg") no-repeat center;
                background-size: contain;
                max-width: 400px;
                height: 250px;
            }
            @include Pad {
                background: url("../images/main-vision-mobile.svg") no-repeat right;
                background-size: 95%;
                max-width: 100%;
                width: 100%;
                height: 230px;
            }
            @include mobile-lg {
                background: url("../images/main-vision-mobile.svg") no-repeat center;
                background-size: 100%;
                margin-bottom: 20px;
                margin-top: 0;
            }
            @include mobile-md {
                height: 200px;
            }
            @include mobile-sm {
                height: 150px;
            }
        }
    }
    &-title {
        background: url("../images/main-title.svg") no-repeat center;
        height: 88px;
        background-size: contain;
        text-indent: -9999px;
        width: 90%;
        max-width: 450px;
        @include PC-lg {
            background: url("../images/main-title.svg") no-repeat top;
            background-size: contain;
            height: 130px;
            margin-bottom: 15px;
        }
        @include PC {
            position: absolute;
            max-width: 280px;
            left: 0;
            top: 100px;
        }
        @include Pad {
            max-width: 200px;
        }
        @include mobile-lg {
            background: url("../images/main-title.svg") no-repeat center;
            background-size: contain;
            width: 85%;
            max-width: 400px;
            position: initial;
            height: 70px;
            margin-bottom: 0;
        }
        @include mobile-md{
            height: 60px;
        }
    }
    &-logo {
        max-width: 320px;
        @include PC-lg {
            max-width: 250px;
        }
        @include PC {
            max-width: 200px;
            margin-top: 20px;
        }
        @include mobile-lg {
            margin-bottom: 20px;
        }
         @include mobile-sm {
            max-width: 180px;
            margin-bottom: 10px;
        }
    }
    &-menu {
        position: relative;
        @include PC {
            margin: 0 auto;
            width: 100%;
        }
    }
    &-menu-item {
        display: flex;
        position: relative;
        width: 780px;
        margin-bottom: 10px;
        margin: 0 auto 10px auto;
        @include PC-lg {
            width: 700px;
        }
        @include PC {
            width: 630px;
        }
        @include Pad {
            width: 530px;
        }
        @include mobile-lg {
            width: 95%;
            flex-wrap: wrap;
        }
        @include mobile-xmd{
            width: 100%;
            flex-wrap: nowrap;
            margin-bottom: 5px;
        }
        li {
            @include mobile-xmd {
                width: 33.33%;
                padding: 0 2px;
            }
        }
        &[data-con="middle"] {
            justify-content: flex-end;
        }
    }
    &-menu-link {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 210px;
        height: 210px;
        font-size: $font-lg;
        border-radius: 6px;
        margin-right: 10px;
        font-weight: $font-bold;
        @include PC-lg {
            width: 190px;
            height: 190px;
        }
        @include PC {
            width: 170px;
            height: 170px;
            font-size: $font-md;
        }
        @include Pad {
            width: 145px;
            height: 145px;
            font-size: $font-md;
            padding: 0!important;
        }
        @include mobile-lg {
            width: 125px;
            height: 125px;
            font-size: $font-smd;
            margin-right: 5px;
        }
        @include mobile-xmd {
            font-size: $font-sm;
            width: 100%;
        }
        @include mobile-sm{
            font-size: $font-xs;
            height: 100px;
        }
        
        img {
            width: 50%;
            @include mobile-lg {
                width: 60%;
            }
            @include mobile-sm{
            width: 50%;
        }
        }
    }
    &-menu-img {
        position: absolute;
        bottom: 0;
        width: 145px;
        @include PC {
            width: 130px;
        }
        @include Pad {
            width: 100px;
        }
        @include mobile-lg {
            width: 90px;
        }
        @include mobile-xmd {
            display: none;
        }
        &[data-con="search"] {
            right: 5px;
        }
        &[data-con="check"] {
            left: -10px;
        }
        &[data-con="speak"] {
            right: 27%;
        }
    }
}
