
/*--------------------loading開始------------------*/
#preloader {/*   這是整個會蓋住畫面的底色色塊  */
  position: fixed;
  width: 100%;
  height: 100vh;
  left: 0%;
  top: 0%;
  background-color: #fff;
  z-index: 9999;
  display: flex;
  justify-content: center;
  align-items: center;
}

.wrapper {
	background: url("../images/bg-page.svg") no-repeat center 10px fixed;
	background-size: 2050px;
	min-height: calc(100vh - 55px);
	@include PC-lg{
		padding-top: 80px;
		padding-bottom: 50px;
	}
	@include PC {
		background: url("../images/bg-page.svg") no-repeat 30%;
		background-size: cover;
		padding-top: 0;
		padding-bottom: 0;
	}
	@include Pad {
		min-height: calc(100vh - 51px);
	}
	@include mobile-lg {
		min-height: calc(100vh - 49px);
	}
}
/*--------------------選單開始------------------*/
.navbar {
	transition: all 0.2s ease;
	background: $primary-color;
	&-toggler {
		img {
			width: 26px;
		}
		&:hover {
			opacity: 0.8;
		}
		&:focus {
			box-shadow: none;
		}
	}
	&-collapse {
		@include Pad {
			padding: 10px 0;
		}
	}
	.btn-input-group {
		@include PC {
			width: 200px;
		}
		@include Pad {
			width: 100%;
			max-width: 300px;
			margin: 0 auto;
		}
	}
}
.navbar-nav {
	margin: 0 auto;
	width: 80%;
	max-width: 1400px;
	display: flex;
	justify-content: space-between;
	@include PC-md {
		width: 90%;
	}
}
.nav-item {
	@include PC-sm {
		border-bottom: 1px solid $primary-light-color;
		&:last-child {
			border-bottom: none;
		}
	}
}
.nav-link {
	padding: 12px 24px !important;
	color: $white;
	@include PC {
		padding: 12px !important;
	}
	@include PC-sm {
		display: flex;
		justify-content: center;
		color: $white;
		padding: 10px 0;
		width: 100%;
		justify-content: center;
		img {
			filter: invert(100%) sepia(0%) saturate(1%) hue-rotate(88deg) brightness(100%) contrast(110%);
			margin-right: 10px;
			width: 24px;
		}
	}
	&:hover {
		background: darken($color: $primary-color, $amount: 5%);
		color: $white;
		opacity: 1 !important;
	}
}
/*------------------------header開始----------------------------*/
.header {
	width: 90%;
	height: 235px;
	margin: 0 auto;
	max-width: 1400px;
	position: relative;
	@include PC-md {
		height: 200px;
	}
	@include PC {
		height: 180px;
	}
	@include PC-sm {
		height: 140px;
	}
	@include Pad {
		height: 125px;
	}
	@include mobile-lg {
		height: 100px;
	}
	& > * {
		position: absolute;
	}
	&-logo {
		width: 250px;
		left: 0;
		top: 16px;
		@include PC-md {
			width: 250px;
		}
		@include PC {
			width: 200px;
		}
		@include Pad {
			width: 170px;
		}
		@include mobile-lg {
			width: 150px;
		}
		@include mobile-sm {
			width: 140px;
		}
	}
	&-title {
		width: 375px;
		left: calc(50% - 500px);
		top: 85px;
		@include PC-md {
			left: calc(50% - 480px);
			width: 400px;
			top: 80px;
		}
		@include PC {
			left: calc(50% - 460px);
			width: 320px;
		}
		@include PC-sm {
			left: calc(50% - 340px);
			width: 280px;
			top: 65px;
		}
		@include Pad {
			left: calc(50% - 260px);
			width: 240px;
			top: 60px;
		}
		@include mobile-lg {
			left: calc(50% - 190px);
			width: 200px;
			top: 50px;
		}
		@include mobile-md {
			width: 190px;
			left: calc(50% - 170px);
		}
		@include mobile-sm {
			width: 160px;
			left: calc(50% - 145px);
		}
	}
	&-vision {
		width: 510px;
		right: calc(50% - 430px);
		top: 27px;
		@include PC-md {
			right: calc(50% - 400px);
			width: 460px;
			top: 10px;
		}
		@include PC {
			right: calc(50% - 320px);
			width: 430px;
			top: 5px;
		}
		@include PC-sm {
			width: 380px;
			right: calc(50% - 350px);
		}
		@include Pad {
			width: 250px;
			right: calc(50% - 270px);
			top: 20px;
		}
		@include mobile-lg {
			width: 200px;
			right: calc(50% - 210px);
		}
		@include mobile-md {
			width: 150px;
			right: calc(50% - 170px);
			top: 30px;
		}
		@include mobile-sm {
			width: 130px;
			right: calc(50% - 150px);
			top: 40px;
		}
	}
	&-link {
		display: flex;
		right: 0;
		bottom: 12px;
		@include PC-sm {
			bottom: 10px;
		}
		&-item {
			color: $primary-color;
			@include PC-sm {
				color: $white;
			}
			&:hover {
				color: $primary-light-color;
				@include PC-sm {
					color: $secondary-light-color;
				}
			}
		}
		@include PC {
			font-size: $font-xs;
		}
		@include PC-sm {
			bottom: -33px;
			color: $white;
			z-index: 10;
		}
		&-img {
			width: 22px;
			@include PC {
				width: 18px;
			}
			@include PC-sm {
				filter: invert(100%) sepia(0%) saturate(7498%) hue-rotate(186deg) brightness(110%) contrast(101%);
			}
		}
	}
}
.marquee {
	font-size: $font-sm;
	color: $primary-light-color;
	width: 100%;
	max-width: 1280px;
	display: flex;
	@include PC-lg {
		width: 1160px;
	}
	@include PC {
		width: 610px;
		margin-bottom: 16px;
	}
	@include Pad {
		font-size: $font-xs;
		width: 510px;
	}
	@include mobile-lg {
		width: 88%;
		font-size: $font-xxs;
		margin-bottom: 0;
	}

	&[data-con="page"] {
		color: $white;
		width: 100%!important;
		max-width: 100%!important;
		font-weight: $font-light;
		font-size: $font-xs;
		img {
			filter: invert(100%) sepia(0%) saturate(50%) hue-rotate(27deg) brightness(600%) contrast(99%);
		}
	}
}
/*------------------------footer開始----------------------------*/
.gotop {
	display: block;
	position: fixed;
	right: calc(5% - 22px);
	bottom: 60px;
	background: url(../images/gotop.svg);
	cursor: pointer;
	z-index: 10;
	@include PC {
		right: calc(5% - 20px);
	}
	@include Pad {
		right: 10px;
	}
	@include mobile-sm {
		right: 5px;
		bottom: 75px;
	}
	&:hover {
		opacity: 0.8;
	}
	img {
		width: 45px;
		@include PC {
			width: 40px;
		}
		@include Pad {
			width: 35px;
		}
		@include mobile-sm {
			width: 30px;
		}
	}
}
footer {
	font-size: $font-xs;
	line-height: 1.3;
	@include mobile-lg {
		font-size: $font-xxs;
	}
	@include mobile-md {
		font-size: $font-xxxs;
		padding-left: 10px;
		padding-right: 10px;
	}
}
/*--------------------視窗開始------------------*/
.modal-content {
	border-radius: 30px;
	border: 2px solid $white;
	overflow: hidden;
	label {
		color: $primary-color;
		font-weight: $font-bold;
	}
}
.modal-title {
	display: flex;
	@include mobile-lg {
		font-size: $font-smd;
	}
	img {
		width: 24px;
		margin-right: 8px;
	}
}
/*------------------------內容區塊開始----------------------------*/
.content {
	width: 90%;
	min-height: calc(100vh - 387px);
	margin: 0 auto;
	max-width: 1400px;
	padding: 16px 44px;
	border: 1px solid $gray-xlight;
	border-bottom: 0;
	margin-top: 16px;
	border-radius: 12px 12px 0 0;
	background: $white;
	position: relative;
	@include PC-md{
		min-height: calc(100vh - 352px);
	}
	@include PC {
		padding: 16px 32px;
		min-height: calc(100vh - 332px);
	}
	@include PC-sm {
		padding: 16px 24px;
		min-height: calc(100vh - 290px);
	}
	@include Pad {
		width: 95%;
		padding: 16px;
		min-height: calc(100vh - 275px);
	}
	@include mobile-lg {
		width: 100%;
		border: none;
		margin-top: 0;
		border-radius: 0;
		min-height: calc(100vh - 232px);
	}
	@include mobile-sm {
		min-height: calc(100vh - 246px);
	}
	&-bottom-btn {
		border-radius: 80px;
		box-shadow: 0 0 12px $gray-light;
		bottom: 16px;
		display: inline-flex;
		background: $white;
		color: $gray;
		padding: 8px 24px;
		margin: 30px 0 16px 0;
		@include Pad {
			padding: 4px 16px;
			font-size: $font-xs;
		}
		&:hover {
			box-shadow: none;
			color: $black;
		}
		img {
			width: 24px;
			@include Pad {
				width: 18px;
			}
		}
	}
}
/*------------------------表格開始----------------------------*/
.rwd-table {
	overflow: hidden;
	min-width: 100%;
	tr:nth-of-type(2n) {
		background: darken($color: $gray, $amount: 5%);
	}
	tr {
		@include mobile-lg {
			background: #012d46;
		}
	}
	th,
	td {
		margin: 0.5em 1em;
		text-align: left;
		border-top: 1px solid $primary-color;
		@include mobile-lg {
			border: none;
			margin: 0 1em;
		}
	}
	th {
		background: $secondary-color;
		color: $primary-color;
		font-weight: 500;
		@include mobile-lg {
			display: none;
		}
	}
	td {
		background: none;
		color: $white;
		text-align: center;
		@include mobile-lg {
			display: block;
			text-align: left;
		}
		&:before {
			content: attr(data-th) " : ";
			display: none;
			@include mobile-lg {
				display: inline-block;
				color: $primary-color;
			}
		}
	}
}
.footable {
	background: $white;
	/*tr[data-expanded="true"]{
		background: $secondary-color;
		color: $white;
		.fooicon-minus:before{
			background: url(../images/ic-minus-light.svg) no-repeat;
			background-size: contain;
		}
	}*/
	.footable-detail-row {
		background: $white;
	}
	.footable-toggle{
        margin-right: 0;
        margin-left: 4px !important;
    }
	.footable-details {
		background: $gray-xxlight;
		border-radius: 0;
		tbody {
			tr {
				border-bottom: 1px solid $white;
			}
			th {
				background: $secondary-color;
				color: $white;
				vertical-align: middle;
			}
			td {
				text-align: left;
				height: 40px;
			}
		}
	}
}
.info-table-wrapper {
	height: calc(100% - 50px);
	overflow: auto;
	thead {
		position: sticky;
		top: -2px;
		z-index: 1;
	}
}
.filter-btn {
	&.collapsed {
		background: $primary-color !important;
		border: 1px solid $primary-color !important;
	}
}

#filter-box {
	position: absolute;
	right: 16px;
	top: 60px;
	z-index: 3;
	width: 345px;
	box-shadow: 0 0 8px $gray;
	border: 0;
	border-radius: 5px;
	background: $white;
	@include mobile-lg {
		width: 280px;
		top: 100px;
		right: 10px;
	}
	&:before {
		content: "";
		position: absolute;
		display: block;
		width: 22px;
		height: 13px;
		background: url("../images/ic-point.svg") no-repeat;
		right: 12px;
		top: -13px;
		@include mobile-lg {
			right: 5px;
		}
	}
}
