/*-----------------連結樣式---------------------*/
@each $key, $value in $color-themes {
    .link-#{$key} {
        color: $value;
        &:hover {
            color: darken($value, 20%);
            img {
                filter: brightness(0.8);
            }
        }
    }
}
.link-img {
    &:hover {
        opacity: 0.8;
    }
}
.link-shadow {
    &:hover {
        box-shadow: 0 0 10px $gray-light !important;
    }
}
.nav-tabs {
    border-bottom: 2px solid $primary-light-color;
    .nav-item {
        margin-right: 10px;
    }
    .nav-link {
        color: $primary-color;
        border-radius: 10px 10px 0 0;
        border: 0;
        margin-bottom: -2px;
        &:hover {
            color: $primary-color;
            background: $gray-xxxlight;
        }
        &.active {
            background: $primary-light-color;
            color: #fff;
            border: 1px solid $primary-light-color;
        }
    }
}
/*-----------------文字樣式---------------------*/
@each $key, $value in $color-themes {
    //文字顏色
    .#{$key} {
        color: $value !important;
    }
}
@each $key, $value in $font-size-themes {
    //文字尺寸
    .#{$key} {
        font-size: $value !important;
    }
}
@each $key, $value in $font-size-themes {
    //文字尺寸
    .#{$key}-lg {
        @include PC {
            font-size: $value !important;
        }
    }
    .#{$key}-md {
        @include Pad {
            font-size: $value !important;
        }
    }
}
@each $key, $value in $font-weight-themes {
    //文字重量
    .#{$key} {
        font-weight: $value !important;
    }
}
.txt-shadow {
    text-shadow: 0 0 5px $gray;
}
.ls-0 {
    letter-spacing: normal;
}
/*-----------------背景樣式---------------------*/
@each $key, $value in $color-themes {
    .bg-#{$key} {
        background: $value !important;
    }
}
/*-----------------按鈕樣式---------------------*/
@each $key, $value in $color-themes {
    .btn-dark-#{$key} {
        @include btn-dark($value);
        display: flex;
        justify-content: center;
        align-items: center;
    }
}
@each $key, $value in $color-themes {
    .btn-border-#{$key} {
        @include btn-border($value);
        display: flex;
        justify-content: center;
        align-items: center;
    }
}
.toggle-menu {
    display: flex;
    width: 200%;
    border-bottom: 5px solid $danger-color;
    flex-wrap: nowrap;
    @include PC {
        border-bottom: 3px solid $danger-color;
    }
    li {
        flex-shrink: 0;
    }
    &-item {
        display: block;
        border-radius: 6px 6px 0 0 !important;
        background: $gray-light !important;
        border: none !important;
        color: $white;
        letter-spacing: 1px;
        font-size: $font-smd;
        padding: 8px 24px !important;
        cursor: pointer;
        @include PC {
            font-size: $font-sm;
            padding: 6px 16px !important;
        }
        @include Pad {
            font-size: $font-xs;
        }
        @include mobile-lg {
            padding: 6px 12px !important;
        }
        &:hover {
            color: $white;
        }
        &.active {
            color: $white !important;
            background: $danger-color !important;
            border: none !important;
            pointer-events: none;
        }
    }
}
.icon-toggle-menu {
    border-bottom: 0;
    position: relative;
    &::after {
        content: " ";
        display: block;
        width: 1px;
        height: 100%;
        position: absolute;
        left: 50%;
        background: $primary-color;
    }
    &[data-list="none"]{
        &:after{
            display: none;
        }
        .icon-toggle-menu-item{
            &:after{
                display: none;
            }
        }
    }
    &-item {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: $font-lg;
        font-weight: $font-bold;
        color: $base-color;
        @include Pad {
            font-size: $font-md;
        }
        @include mobile-lg {
            font-size: $font-sm;
        }
        &:hover {
            color: $gray-dark;
        }
        &.active {
            color: $primary-color;
            pointer-events: none;
            &::after {
                background: url("../images/ic-arrow-active.svg");
                background-size: contain;
            }
        }
        &::after {
            content: " ";
            display: block;
            width: 18px;
            height: 18px;
            background: url("../images/ic-arrow.svg");
            background-size: contain;
            margin-left: 5px;
        }
        img {
            @include PC {
                width: 100px;
                margin-right: 5px;
            }
            @include mobile-lg {
                width: 60px;
            }
            @include mobile-md {
                display: none;
            }
        }
    }
}
.circle-btn {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}
.mobile-menu {
    &-item {
        color: $primary-color;
        font-weight: $font-bold;
        padding: 4px 24px;
        border-right: 1px solid $primary-color;
        &:last-child {
            border: none;
        }
    }
}
.icon-btn {
    display: inline-block;
    width: 24px;
    vertical-align: inherit;
    margin: 0 5px;
    cursor: pointer;
    &:hover {
        opacity: 0.8;
    }
}
.icon-txt-btn {
    display: flex;
    align-items: center;
    justify-content: center;
}
.btn-box {
    @include Pad {
        font-size: $font-xs !important;
    }
    &-item {
        min-height: 45px;
    }
}
.btn-list {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding-bottom: 15px;
    height: 100%;
    &-header {
        display: flex;
        width: 100%;
        justify-content: center;
        padding-top: 5px;
        color: $primary-color;
        height: 40px;
        border-bottom: 1px solid lighten($color: $primary-color, $amount: 30%);
    }
    &-item {
        color: $gray-dark;
        text-decoration: underline;
        display: block;
        padding: 4px 0 4px 8px;
        &:hover {
            text-decoration: none;
            color: $primary-color;
        }
    }
}
.tab-group {
    height: 38px;
    background: $gray-xxlight;
    border-radius: 5px;
    display: inline-flex;
    align-items: center;
    position: relative;
    color: $primary-color;

    input {
        display: none;
    }

    label {
        border-radius: 5px;
        display: block;
        height: 100%;
        padding: 0 16px;
        font-size: $font-sm;
        display: flex;
        align-items: center;
        cursor: pointer;
        color: $primary-color;
        transition: all 0.5s ease;
        white-space: nowrap;
        font-weight: $font-bold;
        margin-bottom: 0 !important;
        &:hover {
            color: $primary-color;
        }
        @include PC {
            font-size: $font-xs;
        }
    }

    > input:checked + label {
        color: $white;
        background: $primary-color;
    }
}

.tab-menu {
    border-radius: 5px;
    display: inline-flex;
    align-items: center;
    position: relative;
    color: $primary-color;
    @include mobile-lg {
        flex-wrap: wrap;
    }

    input {
        display: none;
    }

    label {
        border-radius: 5px;
        transition: all 0.5s ease;
        display: block;
        height: 38px;
        margin: 0 32px !important;
        font-size: $font-smd;
        display: flex;
        align-items: center;
        cursor: pointer;
        color: $primary-color;
        white-space: nowrap;
        font-weight: $font-bold;
        @include PC {
            font-size: $font-sm;
        }
        @include PC-sm {
            margin: 0 20px !important;
        }
        @include mobile-lg {
            width: calc(50% - 30px);
            margin: 0 10px 0 20px !important;
        }
        &:hover {
            color: $primary-color;
        }
    }

    > input:checked + label {
        position: relative;
        &:before {
            position: absolute;
            left: -25px;
            content: "";
            display: block;
            width: 20px;
            height: 20px;
            border-radius: 50%;
            border: 6px solid $secondary-light-color;
            @include PC {
                width: 12px;
                height: 12px;
                left: -18px;
                top: 14px;
                border: 3px solid $secondary-light-color;
            }
        }
    }
}

/*-----------------邊框樣式---------------------*/
@each $key, $value in $color-themes {
    .bd-#{$key} {
        border-color: $value !important;
    }
}
/*-----------------標題樣式---------------------*/
.title {
    display: flex;
    color: $primary-color;
    align-items: center;
    justify-content: center;
    position: relative;
    margin-bottom: 20px;
    z-index: 1;
    @include PC {
        font-size: $font-xl;
    }
    @include PC-sm {
        font-size: $font-lg;
    }
    &:after {
        content: "";
        display: block;
        position: absolute;
        background: $primary-color;
        width: 100%;
        height: 2px;
        left: 0;
        top: 35px;
        @include PC {
            height: 1px;
        }
    }
    &-con {
        background: $white;
        display: flex;
        align-items: center;
        z-index: 10;
        padding: 0 20px;
        @include PC {
            padding: 0 16px;
        }
        @include PC-sm {
            padding: 0 12px;
        }
    }
    img {
        width: 73px;
        margin-right: 8px;
        filter: invert(30%) sepia(14%) saturate(1755%) hue-rotate(200deg) brightness(45%) contrast(140%);
        @include PC {
            width: 60px;
        }
        @include PC-sm {
            width: 50px;
        }
    }
}
.subheading {
    background: $primary-color;
    color: $white;
    display: inline-block;
    font-size: $font-lg;
    padding: 6px 40px;
    font-weight: $font-light;
}
.subtitle {
    color: $primary-color;
    display: block;
    padding: 12px 0;
    position: relative;
    font-size: $font-lg;
    font-weight: $font-heavy;
    @include PC-sm {
        font-size: $font-smd;
    }
}
.sub-subtitle {
    color: $primary-color;
    font-size: $font-smd;
    font-weight: $font-bold;
    padding: 0 12px;
    &:before {
        content: " ";
        display: inline-block;
        margin-right: 7px;
        background: url("../images/ic-title-arrow.svg") no-repeat bottom;
        background-size: contain;
        width: 18px;
        height: 18px;
    }
    @include PC-sm {
        font-size: $font-sm;
    }
}
.icon-title {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: $font-lg;
    color: $primary-color;
    margin-bottom: 0;
    @include PC {
        font-size: $font-md;
    }
    img {
        width: 145px;
        @include PC {
            width: 120px;
        }
    }
}
/*-----------------列表樣式---------------------*/
.accordion {
    &-body {
        position: relative;
        @include Pad {
            font-size: $font-xs;
        }
        &:before {
            content: "A.";
            display: block;
            position: absolute;
            left: 20px;
            top: 0;
            color: $secondary-light-color;
            font-size: $font-lg;
            @include Pad {
                display: none;
            }
        }
    }
    &-item {
        border: none;
    }
    &-title{
        color: $primary-color;
        font-size: $font-md;
        border-left: 4px solid $primary-color;
        padding-left: 12px;
        margin-bottom: 12px;
    }
    &-subtitle{
        color: $primary-light-color;
        font-size: $font-sm;
        padding: 8px 0 8px 16px;
        font-weight: $font-light;
        border-top: 1px solid $primary-light-color;
        border-bottom: 1px solid $primary-light-color;
        line-height: 1.5;
    }
    &-button {
        background: $gray-xxlight;
        color: $gray;
        margin-bottom: 16px;
        border-radius: 8px !important;
        padding: 12px 45px 12px 18px;
        position: relative;
        line-height: 1.3;
        @include Pad {
            padding: 12px 40px 12px 12px;
            font-size: $font-xs;
            margin-bottom: 12px;
        }
        &:after {
            background: url(../images/ic-arrow-open.svg);
            background-size: 100%;
            position: absolute;
            right: 18px;
            top: calc(50% - 10px);
            @include Pad {
                right: 12px;
                width: 18px;
                height: 18px;
                top: calc(50% - 9px);
            }
        }
        &:not(.collapsed) {
            background: $secondary-light-color;
            color: $white;
        }
        &:not(.collapsed)::after {
            background: url(../images/ic-arrow-close.svg);
            background-size: 100%;
            transform: rotate(0deg);
        }
    }
    &-id {
        font-size: $font-lg;
        margin-right: 12px;
        @include Pad {
            margin-right: 8px;
            font-size: $font-md;
        }
        span {
            font-size: $font-sm;
            margin-left: 3px;
        }
    }
    &-pic {
        width: 100%;
        margin: 16px auto;
    }
    &-pic-inline {
        display: inline;
    }
    &-list {
        li {
            margin-bottom: 10px;
        }
        &-item{
            display: flex;
            span{
                width: 24px;
                margin-right: 4px;
                flex-shrink: 0;
                text-align: right;
            }
        }
    }
    &-btn {
        display: inline-flex;
        img {
            width: 20px;
            @include Pad {
                width: 16px;
            }
        }
    }
}
.accordion[data-con="share"]{
    .accordion-body{
        &:before{
            display: none;
        }
    }
}
.accordion-base {
    .accordion-button:not(.collapsed) {
        background: $primary-light-color;
    }
    .accordion-body{
        &:before{
            display: none;
        }
    }
}
.accordion-stable {
    .accordion-button:not(.collapsed) {
        background: $stable-xlight-color;
    }
    .accordion-body:before {
        color: $stable-xlight-color;
    }
}
.card-list {
    &-item:nth-child(odd) {
        .card-list-link {
            background: $secondary-light-color;
            &:hover {
                background: $primary-light-color;
            }
        }
    }
    &-item:nth-child(even) {
        .card-list-link {
            background: darken($color: $gray-light, $amount: 6%);
            &:hover {
                background: darken($color: $gray-light, $amount: 10%);
            }
        }
    }
    .card-list-link {
        border-radius: 8px;
        color: $white;
        padding: 20px;
        display: flex;
        align-items: center;
        @include PC-sm {
            padding: 16px;
            font-size: $font-xsm;
        }
        @include Pad {
            font-size: $font-xs;
        }
    }
    .card-list-number {
        display: block;
        border-right: 1px solid $white;
        font-size: $font-xxl;
        opacity: 0.5;
        padding-right: 16px;
        font-weight: $font-xlight;
        @include PC-sm {
            font-size: $font-xl;
            padding-right: 12px;
            font-weight: $font-light;
        }
        @include Pad {
            font-size: $font-lg;
        }
    }
    .card-list-txt {
        width: calc(100% - 90px);
        padding: 0 16px;
        @include over_text(2);
        @include PC-sm {
            width: calc(100% - 70px);
            padding: 0 12px;
        }
        @include Pad {
            width: calc(100% - 55px);
        }
    }
    .card-list-icon {
        width: 45px;
        @include PC-sm {
            width: 35px;
        }
        @include Pad {
            width: 30px;
        }
    }
}
.card-list-stable {
    .card-list-item:nth-child(odd) {
        .card-list-link {
            background: $stable-xlight-color;
            &:hover {
                background: $stable-light-color;
            }
        }
    }
}

.number-list {
    list-style-type: decimal;
    padding-left: 25px;
    color: lighten($gray-dark, 10%);
    li {
        margin-bottom: 30px;
    }
    li::marker {
        color: $assist-color;
    }
}
.txt-list {
    &-item {
        display: flex;
        align-items: center;
        position: relative;
        padding: 16px 0;
        @include PC-sm {
            flex-direction: column;
            align-items: start;
        }
        ol {
            list-style-type: decimal-leading-zero;
            padding-left: 26px;
            li{
                margin-bottom: 8px;
            }
            li::marker {
                color: $assist-color;
            }
        }
        &:after {
            content: "";
            background: $secondary-light-color;
            opacity: 0.6;
            position: absolute;
            width: calc(100% - 222px);
            height: 1px;
            right: 0;
            bottom: 0;
            @include PC-sm {
                width: 100%;
            }
        }
        &:last-child {
            &:after {
                display: none;
            }
        }
    }
    &-title {
        width: 190px;
        margin-right: 32px;
        margin-bottom: 0;
        text-align: right;
        color: $primary-color;
        font-weight: $font-bold;
        font-size: $font-sm;
        flex-shrink: 0;
        @include PC {
            font-size: $font-xsm;
            width: 170px;
        }
        @include PC-sm {
            width: 100%;
            text-align: left;
            margin-bottom: 8px;
            margin-right: 0;
        }
    }
    &-txt {
        color: $gray-dark;
        img{
            width: 20px;
        }
    }
    &-btn{
        height: auto !important;
        img{
            width: 6px !important;
            margin-left: 8px;
            opacity: 0.6;
        }
    }
}
.txt-btn-list {
    li {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @include Pad {
            flex-direction: column;
            align-items: flex-start;
        }
    }
    .txt-btn-list-txt {
        width: calc(100% - 158px);
        @include Pad {
            margin-bottom: 10px;
            width: 100%;
        }
        &-en {
            width: calc(100% - 295px);
            @include PC {
                margin-bottom: 10px;
                width: 100%;
            }
        }
    }
}
.tag-list {
    li {
        display: flex;
        align-items: flex-start;
        &:last-child {
            border-bottom: none;
        }
        .tag-list-tag {
            background: $primary-xlight-color;
            color: $gray-dark;
            padding: 0 8px;
            border-radius: 5px;
            margin-right: 15px;
            font-size: $font-sm;
            text-align: center;
            flex: 0 0 90px;
        }
    }
}
.tag-list-header {
    border-radius: 30px 30px 0 0;
    background: $gray;
    margin-bottom: 0;
    padding: 10px;
}
.list-group-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-left-width: 5px !important;
    @include mobile-md {
        flex-direction: column;
        align-items: start;
        &.list-group-header {
            display: none;
        }
    }
    &:hover {
        background: $secondary-color;
    }
}
.list-badge {
    border-left: 1px solid $gray;
    padding: 0 0 0 20px;
    color: $secondary-color;
    @include Pad {
        padding: 0 0 0 10px;
    }
}
.disabled {
    pointer-events: none !important;
}
/*-----------------區塊樣式---------------------*/
.form-box {
    border-radius: 8px;
    padding: 24px 0;
    position: relative;
    z-index: 5;
    @include PC-sm {
        padding: 16px 0;
    }
    @include Pad {
        margin-top: 50px;
    }
    &-img {
        position: absolute;
        left: 0;
        bottom: -24px;
        width: 270px;
        @include PC {
            width: 240px;
            left: 40px;
        }
        @include PC-sm {
            width: 200px;
            bottom: -16px;
        }
        @include Pad {
            width: 150px;
            bottom: 34px;
            left: calc(50% - 100px);
        }
    }
    &-input {
        height: 42px;
        @include PC-sm {
            height: 35px;
        }
    }
    &-btn {
        height: 42px;
        @include PC-sm {
            font-size: $font-xsm;
            height: 35px;
        }
        @include Pad {
            padding: 0 12px;
            width: 100%;
            img {
                width: 16px;
            }
        }
        @include mobile-md {
            font-size: $font-xs;
            padding: 0 8px;
            width: 100%;
        }
        @include mobile-sm {
            p {
                display: none;
            }
        }
    }
}
.info-box {
    border-radius: 8px;
    border: 1px solid $secondary-light-color;
    border-left: 6px solid $secondary-light-color;
    padding: 12px 16px;
    color: $tertiary-color;
}
.notice-box {
    display: inline-block;
    padding: 2px 15px;
    font-size: $font-xs;
    border-radius: 6px;
    background: $gray-xlight;
}
/*---------------------表格樣式---------------------*/
@each $key, $value in $color-themes {
    //文字顏色
    .table-bd-#{$key} {
        th,
        td {
            border-color: $value;
        }
    }
}
.modal {
    .table-wrapper {
        padding: 0;
        height: 530px;
        thead {
            position: sticky;
            top: 0;
            z-index: 1;
        }
        td {
            height: 50px;
        }
    }
}
.table {
    color: $gray-dark;
    font-size: $font-xs;
    border-radius: 10px 10px 0 0;
    overflow: hidden;
    th {
        background: $assist-color;
        color: $white;
        font-weight: $font-light;
        text-align: center;
        position: sticky;
        padding: 12px 0;
    }
    td {
        text-align: center;
        vertical-align: middle;
    }
    th[data-con="action"] {
        width: 100px;
        @include PC {
            width: 150px;
        }
        @include mobile-md {
            width: 250px;
        }
    }
    & > :not(:first-child) {
        border-top: none;
    }
    .btn {
        opacity: 0.8;
        border-radius: 5px !important;
        display: inline-block;
        &:hover {
            opacity: 1;
        }
    }
}
.table-rwd {
    min-width: 100%;
    th {
        display: table-cell;
        border-color: $white;
        @include Pad {
            display: none;
        }
    }
    td {
        display: table-cell;
        border-color: $gray;
        @include Pad {
            display: flex;
            flex-direction: column;
            text-align: left;
            display: block;
            border: 0 !important;
            padding: 10px 20px 10px 20px !important;
            position: relative;
            &:after {
                content: "";
                display: block;
                position: absolute;
                width: calc(100% - 40px);
                height: 1px;
                left: 20px;
                bottom: 0;
                background: $gray;
            }
            &.table-head:first-child {
                background: $gray;
                color: $white;
                font-weight: $font-light;
                &:before {
                    display: inline-block;
                    color: $white;
                    background: none;
                    padding: 0;
                    font-weight: $font-light;
                }
            }
            &:last-child {
                &:after {
                    display: none;
                }
            }
        }
        &:before {
            display: none;
            @include Pad {
                content: attr(data-th);
                display: inline-block;
                padding: 2px 10px;
                color: $white;
                font-size: $font-xs;
                background: lighten($gray, 15%);
                border-radius: 15px;
            }
        }
    }
}
.table-link {
    display: block;
    width: calc(100% + 1.5em);
    height: calc(100% + 1.5em);
    margin: -0.75em;
    padding: 0.75em;
    color: $gray-dark;
    border: 1px solid transparent;
    position: relative;
    &:after {
        position: absolute;
        content: "";
        width: 18px;
        height: 18px;
        background: url(../images/icon-plus.svg) no-repeat center;
        background-size: contain;
        right: 5px;
        bottom: 5px;
        opacity: 0.4;
    }
    &:hover {
        border: 1px solid $primary-color;
        background: $white;
        color: $gray-dark;
    }
}
.table-height {
    //讓td內容器可以高100%
    td {
        height: 1px;
    }
}
.table-item {
    display: inline-block;
    background: $white;
    border-radius: 4px;
    padding: 3px 2px 3px 10px;
    border: 1px solid $primary-color;
    .btn {
        padding: 5px;
        font-size: $font-xs;
        color: $danger-color;
        opacity: 0.8;
        &:hover {
            opacity: 1;
        }
        img {
            width: 10px;
        }
    }
}
.table-form {
    .table-label {
        background: $gray;
        color: $white !important;
    }
    .table-sub-label {
        font-size: $font-xxs;
        display: block;
        color: darken($color: $pale-color, $amount: 10%);
    }
    .form-control,
    .form-select {
        font-size: $font-sm;
    }
    td {
        vertical-align: middle;
    }
}
/*---------------------捲軸樣式---------------------*/
.scroll {
    width: 100%;
    overflow: auto;
    position: relative;
    -webkit-overflow-scrolling: touch;
    &::-webkit-scrollbar-track {
        background-color: $gray-xxlight;
    }
    &::-webkit-scrollbar {
        width: 3px;
        height: 3px;
        background-color: $gray-xxlight;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: $gray-xlight;
    }
}
.scroll-light {
    width: 100%;
    overflow: auto;
    position: relative;
    -webkit-overflow-scrolling: touch;
    &::-webkit-scrollbar-track {
        background-color: $white;
        border: 1px solid $gray-light;
        border-radius: 10px;
    }
    &::-webkit-scrollbar {
        width: 6px;
        height: 6px;
        background-color: $white;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: lighten($color: $gray-light, $amount: 15%);
    }
}
.scroll-border {
    width: 100%;
    overflow: auto;
    position: relative;
    -webkit-overflow-scrolling: touch;
    &::-webkit-scrollbar-track {
        background-color: $tertiary-color;
        border-radius: 15px;
    }
    &::-webkit-scrollbar {
        width: 6px;
        height: 6px;
        background-color: $white;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: $white;
        border: 1px solid $tertiary-color;
    }
}
.scrollx-none {
    overflow-x: hidden;
}

.item_text {
    display: inline-block;
    background: $secondary-color;
    color: lighten($color: $primary-color, $amount: 15%);
    padding: 0px 10px;
    margin: 5px 10px 5px 0;
    border-radius: 5px;
}

.info-line {
    //線條小標
    display: flex;
    justify-content: space-between;
    position: relative;
    &::after {
        content: "";
        display: block;
        position: absolute;
        background: $gray-dark;
        width: 100%;
        height: 1px;
        left: 0;
        top: 12px;
        z-index: 50;
    }
    span {
        background: $white;
        position: relative;
        z-index: 100;
        padding: 0 5px;
    }
}
.article-title {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid $gray-dark;
    font-weight: $font-bold;
    padding-bottom: 15px;
    flex-wrap: wrap;
    @include PC {
        flex-direction: column-reverse;
        font-size: $font-xl;
    }
    @include PC {
        font-size: $font-lg;
        font-weight: $font-heavy;
        padding-bottom: 15px;
    }
}
.article-text {
    line-height: 1.8;
    margin-bottom: 35px;
}

.line-higher {
    line-height: 1.8;
    @include mobile-lg {
        line-height: 1.5;
    }
}
.map-title {
    background: lighten($color: $secondary-color, $amount: 25%);
    color: $primary-color;
    @include PC {
        font-size: $font-md;
    }
}
.media-title {
    @include mobile-lg {
        font-size: $font-xs;
    }
}
.basic-title {
    font-size: $font-sm;
    font-weight: $font-heavy;
}
/*-----------------列表樣式---------------------*/
.menu-card {
    &-item {
        display: flex;
        flex-direction: column;
        background: $assist-color;
        border-radius: 8px;
        align-items: center;
        overflow: hidden;
        &:hover {
            background: darken($color: $assist-color, $amount: 10%);
            .menu-card-bottom {
                background: $gray-xxlight;
            }
        }
    }
    &-img {
        width: 185px;
        height: 185px;
        border-radius: 50%;
        border: 8px solid $gray-xxlight;
        background: $white;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 36px 0;
        @include PC-sm {
            width: 160px;
            height: 160px;
            border: 6px solid $gray-xxlight;
        }
        @include Pad {
            width: 120px;
            height: 120px;
            margin: 24px 0;
        }
        @include mobile-lg {
            width: 100px;
            height: 100px;
            margin: 16px 0;
            border: 4px solid $gray-xxlight;
        }
        img {
            width: 120px;
            @include PC-sm {
                width: 100px;
            }
            @include Pad {
                width: 80px;
            }
            @include mobile-lg {
                width: 70px;
            }
        }
    }
    &-bottom {
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: space-between;
        background: $gray-xxxlight;
        color: $gray;
        padding: 16px 24px;
    }
    &-title {
        font-size: $font-md;
        @include PC-sm {
            font-size: $font-smd;
        }
        @include Pad {
            font-size: $font-sm;
        }
    }
    &-icon {
        width: 26px;
        @include PC-sm {
            width: 20px;
        }
    }
}
.img-list {
    &-box {
        border-radius: 10px;
        border: 4px solid $primary-xlight-color;
        overflow: hidden;
        height: 90px;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        &:hover {
            border: 4px solid $primary-light-color;
        }
        img {
            height: 100%;
            position: absolute;
            @include PC-sm {
                width: 100%;
                height: auto;
            }
        }
    }
    &-add {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        border-radius: 10px;
        border: 2px dashed $primary-light-color;
        color: $primary-weak-color;
        font-size: $font-xs;
        height: 90px;
        &:hover {
            color: $primary-color;
            border: 2px dashed $primary-color;
            img {
                filter: invert(50%) sepia(79%) saturate(1675%) hue-rotate(185deg) brightness(100%) contrast(88%);
            }
        }
    }
}
.icon-list {
    border-top: 1px solid $gray-dark;
    .icon-list-item {
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid $gray-dark;
        padding: 8px 0;
        p {
            display: flex;
        }
        img {
            width: 30px;
            margin-right: 15px;
        }
    }
}
.news-list {
    border-top: 1px solid $gray-dark;
    .news-list-item {
        border-bottom: 1px solid $gray-dark;
        a {
            width: 100%;
            padding: 19px 0;
            display: flex;
        }
        P {
            width: calc(100% - 110px);
            @include over-text(1);
        }
    }
    .news-list-date {
        color: $primary-color;
        width: 110px;
    }
}
.mark-menu {
    border-bottom: 1px solid lighten($color: $secondary-color, $amount: 10%);
    margin-bottom: 25px;
    .mark-menu-item {
        border-right: 1px solid lighten($color: $secondary-color, $amount: 10%);
        &:first-child {
            a {
                border-radius: 8px 0 0 0;
            }
        }
        &:last-child {
            border-right: none;
            a {
                border-radius: 0 5px 0 0;
            }
        }
    }
    a {
        display: block;
        width: 100%;
        background: lighten($color: $secondary-color, $amount: 15%);
        color: $gray-dark;
        position: relative;
        padding: 13px 25px;
        font-size: $font-sm;
        &.active {
            background: $white;
            font-weight: $font-heavy;
            &::after {
                content: "";
                position: absolute;
                width: 100%;
                height: 3px;
                background: $primary-color;
                left: 0;
                bottom: 0;
            }
        }
    }
}
.article-menu {
    border-bottom: 7px solid $tertiary-color;
    a {
        font-weight: $font-bold;
        text-align: center;
        min-width: 140px;
        padding-left: 0;
        padding-right: 0;
    }
}
.text-list {
    padding-left: 25px;
    li {
        list-style-type: decimal;
    }
}
.step-list {
    display: flex;
    flex-wrap: wrap;
    li:nth-child(even) {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0 10px;
    }
    .step-item {
        border: 1px solid $secondary-color;
        padding: 5px 20px;
        @include mobile-lg {
            padding: 4px 13px;
            font-size: $font-xs;
        }
    }
}
.align-btn-list {
    .align-btn {
        width: 100%;
        padding-left: 30%;
        @include PC {
            padding-left: 25%;
        }
        @include mobile-md {
            padding-left: 15%;
        }
        &:hover {
            img {
                -webkit-filter: invert(200%) sepia(4%) saturate(2%) hue-rotate(173deg) brightness(700%) contrast(100%);
                filter: invert(200%) sepia(4%) saturate(2%) hue-rotate(173deg) brightness(700%) contrast(100%);
            }
        }
    }
}
.basic-list {
    @include mobile-md {
        font-size: $font-xs;
        letter-spacing: normal;
    }
}

/* ---------------------頁數開始------------------------*/

.topic-hot-tag {
    li {
        display: inline-block;
        width: 180px;
        max-width: 180px;
        padding: 0 5px;
        margin: 0 0 10px 0;
        @include PC-md {
            width: 33.33%;
        }
        @include Pad {
            width: 50%;
        }
        a {
            display: block;
            width: 100%;
            @include Pad {
                padding: 5px 7px;
                font-size: $font-xxs;
                text-align: center;
            }
            span {
                color: $primary-color;
            }
            &:hover {
                span {
                    color: $white;
                }
            }
        }
        .topic-hot-tag-now {
            span {
                color: $white;
            }
        }
    }
    &-sm {
        li {
            width: 150px;
            max-width: 150px;
            @include PC-md {
                width: 33.33%;
            }
            @include Pad {
                width: 50%;
            }
        }
        a {
            text-align: center;
        }
    }
}
.topic-hot-link {
    li {
        display: inline-block;
    }
    a {
        font-size: $font-xs;
        @include Pad {
            font-size: $font-xxs;
        }
    }
}
/*-----------------區塊樣式---------------------*/
.pagination {
    .page-item {
        padding: 0 2px;
    }
    .page-link {
        border-radius: 3px;
        background: $gray-xxlight;
        color: $gray-dark;
        padding: 0;
        width: 28px;
        height: 28px;
        display: flex;
        justify-content: center;
        align-items: center;
        img {
            width: 15px;
        }
    }
}
.page-item-switch .page-link {
    background: $gray-light;
    border-color: $gray-light;
}
.page-item.disabled .page-link {
    background: $gray-xxlight;
    border-color: $gray-xxlight;
}
.page-item.active .page-link {
    background: $gray;
    border-color: $gray;
}
.event-card {
    box-shadow: 0 0 5px $gray-light;
    padding: 18px;
    &:hover {
        img {
            opacity: 0.5;
            transform: perspective(250px) translateZ(50px);
        }
        .event-card-img-box-text {
            opacity: 1;
            z-index: 100;
        }
    }
    .event-card-img-box-text {
        transition: all 0.2s ease;
        color: $white;
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        opacity: 0;
        padding: 30px;
        pointer-events: none;
        p {
            position: absolute;
            width: 100%;
            left: 0;
            bottom: 25%;
            font-size: $font-xs;
            font-weight: $font-light;
            @include over_text(3);
            padding: 0 10%;
        }
    }
    .event-card-img-box {
        display: block;
        background: $gray-dark;
        position: relative;
        overflow: hidden;
        img {
            transition: all 0.2s ease;
        }
    }
    .event-card-title {
        font-size: $font-md;
        @include over_text(2);
    }
    .event-card-action {
        display: flex;
        justify-content: space-between;
        & > * {
            padding: 5px 25px;
        }
    }
    .event-card-info {
        @include mobile-lg {
            font-size: $font-xs;
            letter-spacing: normal;
        }
        span {
            color: $primary-color;
            font-weight: $font-bold;
            margin-right: 10px;
        }
    }
}
.news-card {
    padding: 30px;
    border: 1px solid $gray-light;
    @include Pad {
        padding: 20px;
    }
    &:hover {
        background: $white;
    }
    .news-card-title {
        font-size: $font-lg;
        font-weight: $font-heavy;
        line-height: 1.4;
        @include over_text(2);
        @include Pad {
            font-size: $font-md;
        }
    }
    .news-card-subtitle {
        font-size: $font-md;
        font-weight: $font-heavy;
        @include Pad {
            font-size: $font-sm;
        }
        @include mobile-lg {
            font-size: $font-xs;
        }
    }
    .news-card-info {
        display: flex;
        justify-content: space-between;
    }
    .news-card-txt {
        p {
            @include over_text(4);
            @include PC-md {
                @include over_text(3);
            }
            @include PC {
                @include over_text(2);
                font-size: $font-xs;
            }
        }
    }
}
.album {
    @include PC {
        height: auto;
    }
    .row {
        height: 100%;
    }
    .album-logo {
        width: 150px;
        @include Pad {
            margin: 15px auto;
        }
    }
    .album-box {
        display: block;
        position: relative;
        &:hover {
            opacity: 0.8;
        }
        .album-icon {
            position: absolute;
            right: 15px;
            bottom: 15px;
            font-size: $font-xl;
        }
    }
}
.topic-card {
    border-bottom: 1px solid $gray-dark;
    &-round {
        border-radius: 8px;
        padding: 18px;
        border: 1px solid $gray-light;
    }
    .topic-title {
        font-size: $font-md;
    }
    .topic-subtitle {
        font-size: $font-sm;
    }
    .topic-mark-box {
        display: flex;
        flex-wrap: wrap;
    }
    .topic-mark {
        color: $white;
        padding: 2px 4px;
        font-size: $font-xxs;
        text-align: center;
        margin-right: 1px;
        font-weight: $font-light;
    }
    .topic-hot-tag {
        font-size: $font-xs;
        li {
            @include mobile-lg {
                width: auto;
                margin: 0 2% 10px 2%;
            }
        }
    }
    .topic-info {
        font-weight: $font-light;
        @include over-text(2);
    }
}
.favorite {
    display: block;
    width: 20px;
    height: 19px;
    @include text-indent;
    &-lg {
        font-size: $font-lg;
    }
    &-line {
        background: url(../images/icon-heart-line.svg) no-repeat center;
        background-size: 100%;
    }
    &-solid {
        background: url(../images/icon-heart-solid.svg) no-repeat center;
        background-size: 100%;
    }
    &-delete {
        background: url(../images/icon-heart-solid-primary.svg) no-repeat center;
        background-size: 100%;
    }
}
.share {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    @include PC {
        width: 100%;
        margin-bottom: 15px;
    }
    a {
        display: block;
        width: 30px;
        height: 30px;
    }
    .favorite {
        height: 28px;
        & + span {
            font-size: $font-xxxs;
        }
    }
}
.mark-box {
    .mark-box-menu {
        display: flex;
        li:first-child {
            a {
                border-radius: 5px 5px 0 0;
            }
        }
        li {
            a {
                display: block;
                padding: 10px 30px 10px 30px;
                background: lighten($color: $secondary-color, $amount: 15%);
                color: $gray-dark;
                border-radius: 0 5px 0 0;
                font-weight: $font-bold;
                margin-top: 4px;
                @include Pad {
                    padding: 8px 20px 8px 20px;
                }
                @include mobile-lg {
                    padding: 8px 10px 8px 10px;
                    font-size: $font-xs;
                }
                &:hover {
                    color: $white;
                }
                &.active {
                    border-radius: 5px 5px 0 0;
                    background: $white;
                    padding: 14px 30px 10px 30px;
                    box-shadow: 0 -2px 3px $gray-light;
                    margin-top: 0;
                    color: $secondary-color;
                    @include Pad {
                        padding: 12px 20px 8px 20px;
                    }
                    @include mobile-lg {
                        padding: 12px 10px 8px 10px;
                    }
                    &:hover {
                        color: $secondary-color;
                    }
                }
            }
        }
    }
    .mark-box-content {
        background: $white;
        border-radius: 0 8px 8px 8px;
        box-shadow: 0 0 4px $gray-light;
        padding: 40px 8%;
    }
    label {
        font-weight: $font-bold;
    }
}
.slide-box-img {
    height: 550px;
    overflow: hidden;
    @include Pad {
        height: auto;
    }
}
.box-content {
    background: $white;
    border-radius: 8px;
    box-shadow: 0 0 4px $gray-light;
    padding: 0 8%;
}
.info-form {
    label,
    .label {
        width: 110px;
        font-weight: bold;
        justify-content: flex-start;
        @include Pad {
            width: 100%;
        }
    }
    .custom-control-label {
        width: auto;
    }
    input,
    select,
    textarea,
    .form-notice {
        width: calc(100% - 110px) !important;
        @include Pad {
            width: 100% !important;
        }
    }
    .form-check-input,
    .form-check-label {
        width: auto !important;
        font-weight: $font-bold;
    }
    .custom-control-label {
        font-weight: normal;
    }

    .custom-select {
        background: url(../images/icon-arrow-down-secondary.svg) no-repeat right top $white;
        &-short {
            width: 230px !important;
            @include PC {
                width: 160px !important;
            }
            @include Pad {
                width: 100% !important;
            }
        }
    }
    .form-control[readonly] {
        background-color: $white;
    }
}

.form-text-box {
    height: 200px;
    border: 1px solid lighten($color: $gray-light, $amount: 15%);
    @include mobile-lg {
        height: 300px;
    }
}
.form-notice {
    margin-left: 130px;
    @include Pad {
        margin-left: 0;
    }
}
.mobile-rounded {
    @include PC {
        border-radius: 10px;
    }
}
.rounded-lg {
    border-radius: 8px;
}
/*-----------------表單樣式---------------------*/
.btn-input-group {
    input {
        border-radius: 50rem;
        padding-left: 20px;
    }
    button {
        border-radius: 0 50rem 50rem 0;
        background: $white;
        border-left: 0;
    }
}
.inside-bar {
    //input內置按鈕
    button {
        padding: 5px;
        border: 1px solid $secondary-color;
        border-radius: 4px;
        width: 35px;
    }
}
.inside-bar-round {
    //input內置按鈕大圓角
    input {
        border-radius: 70px 0 0 70px;
    }
    button {
        padding: 3px 14px;
        border-radius: 0 70px 70px 0;
        width: 50px;
        height: 38px;
    }
}
.input-over-light-base,
.input-over-light-base:focus {
    background-color: lighten($color: $secondary-color, $amount: 20%) !important;
    border-radius: 0;
    border: 0;
}
.custom-select {
    background: url(../images/icon-arrow-down.svg) no-repeat right top $white;
}
.round {
    //大圓角
    border-radius: 70px;
}
.fortime {
    background: url(../images/icon-time.svg) no-repeat right 2px $white !important;
    background-size: 50px;
    padding-right: 50px !important;
}
.fortime-secondary {
    background-image: url(../images/icon-time-secondary.svg);
    background-repeat: no-repeat;
    background-position: right top;
    background-size: 50px;
    padding-right: 50px !important;
}
.steady-checkbox {
    .custom-control-label::before {
        border: 1px solid $tertiary-color;
        border-radius: 0;
    }
    .custom-control-input:checked ~ .custom-control-label::after {
        border: 1px solid $tertiary-color;
        background-color: $tertiary-color;
    }
}

/*-----------------文字樣式---------------------*/
.small-txt {
    color: $gray;
    font-size: $font-xs;
}
.pattern-bg {
    background: url(../images/bg-left.png) no-repeat, url(../images/bg-right.png) no-repeat;
    background-position: left top, right top;
    background-size: 20%, 30%;
}
.letter-spacing-mobile-n {
    @include mobile-lg {
        letter-spacing: normal;
    }
}

/*-----------------連結樣式---------------------*/
.img-link {
    //圖片按鈕
    opacity: 1;
    transition: all 0.2s ease;
    &:hover {
        opacity: 0.8;
    }
}
.shadow-link {
    &:hover {
        box-shadow: none !important;
        transition: all 0.2s ease;
    }
}
.article-menu-select {
    div {
        background: $tertiary-color;
        color: $white;
        height: 38px;
        line-height: 38px;
        padding: 0 20px;
    }
    select {
        border: 1px solid $tertiary-color;
        border-radius: 0;
        min-width: 200px;
    }
}
/*-----------------flex樣式---------------------*/
.flex-unstrech {
    flex: 0 1 auto;
}
