/*------------------------登入頁設定開始----------------------------*/
.wrapper[data-bg="login"]{
  min-height: 100vh;
  background: 
  url("../images/bg-login-top.svg") no-repeat left top,
  url("../images/bg-login-right.png") no-repeat right bottom,
  url("../images/bg-login-left.svg") no-repeat left bottom;
  background-size: 850px,950px,667px;
  @include PC-lg{
    background-size: 800px,60%,600px;
  }
  @include PC-md{
    background-size: 700px,55%,550px;
  }
  @include PC{
    background-size: 650px,50%,500px;
  }
  @include mobile-lg{
    background-size: 200px,80%,250px;
  }
}